<template>
  <div class="editor" ref="editor">
    <div id="toolbar"></div>
    <div id="editor"></div>
    <!--<button v-on:click="getContent"></button>-->
  </div>
</template>

<script>
  import E from 'wangeditor'

  export default {
    props: {
      editorContent: {
        type: String,
        default: '',
        required: false
      },
      base64: {
        type: Boolean,
        default: true,
        required: false
      },
      imageServer: {
        type: String,
        default: '',
        required: false
      },
      config: {
        type: Array,
        default: [
          'head',  // 
          'bold',  // 
          'fontSize',  // 
          'fontName',  // 
          'italic',  // 
          'underline',  // 
          'strikeThrough',  // 
          'foreColor',  // 
          'backColor',  // 
          'link',  // 
          'list',  // 
          'justify',  // 
          'quote',  // 
          'emoticon',  // 
          'image',  // 
          'table',  // 
          'video',  // 
          'code',  // 
          'undo',  // 
          'redo'  // 
        ],
        required: false
      }
    },
    data() {
      return {
        editorInstance: {},
        editorContentInner: ''
      }
    },
    computed: {
      layoutHeight() {
        return this.$store.state.layoutHeight;
      },
      editorStyle() {
        return {
          height: this.$store.state.layoutHeight - 260 + 'px',
        }
      }
    },
    watch: {
      editorContent(value) {
        console.log(value)
        this.editorInstance.txt.html(value)
      },
      config(value){
        console.log(value)
        // debugger
        // this.editorInstance.menus = value;
        // this.editorInstance.create();
      },
      layoutHeight(value) {

      }
    },
    mounted() {
      this.editorInstance = new E(this.$refs.editor);
      this.editorInstance.menus =  [
        'head',
        'bold',
        'italic',
        'underline'
      ];

      if (this.imageServer === '') {
        this.editorInstance.customConfig.uploadImgShowBase64 = this.base64;   //  base64 
      } else {
        this.editorInstance.customConfig.uploadImgServer = (process.env.NODE_ENV === 'production' ? this.$localHref : this.$baseUrl) + this.imageServer;
        this.editorInstance.customConfig.uploadImgParams = {
          //  <=v3.1.0  encode  encode
          //  >=v3.1.1  encode  encode
          // token: 'abcdef12345',
          userName: "admin",
          uId: "123456",
          part: "",
          id: 'WU_FILE_0',
          name: 'dsds',
          type: 'image/jpeg',
          lastModifiedDate: new Date(),
          size: '114111'
        }
      }
      this.editorInstance.customConfig.onchange = (html) => {
        this.editorContentInner = html;
        this.$emit('change', html)
      };
      this.editorInstance.customConfig.zIndex = 0;
      this.editorInstance.create();
      this.getHeight();

    },
    methods: {
      getContent() {
        // alert(this.editorContent)
      },
      getHeight() {
        document.querySelectorAll('.w-e-text-container')[0].style.height = this.layoutHeight - 260 + 'px'
      }
    }
  }
</script>



